<template>
    <div class="signup-form">
        <div style="padding:0 .45rem">
  <div class="header">
            <p class="title"></p>
        </div>
        <div class="fill"></div>
        <div class="info-list username">
            <div class="_icon-username"><i class="icon-username"></i></div>
            <div class="input-item">
                <input class="input" placeholder="" v-model="user.phone"/>
            </div>
        </div>
        <div class="info-list password">
            <div class="_icon-password"><i class="icon-password"></i></div>
            <div class="input-item">
                <input class="input" type="password"  placeholder="" v-model="user.password"/>
            </div>
        </div>
        <div class="info-list password">
            <div class="_icon-password"><i class="icon-duanxinyanzhengma"></i></div>
            <div class="input-item">
                <input class="input" placeholder="" v-model="user.smcode"/>
            </div>
            <div class="smcode-btn" @click="getCode">{{smcodebtnTxt}}</div>
        </div>
        <div class="rule">
            <pai-checkbox v-model="checkActive" @change="handler(checkActive, $event)"></pai-checkbox>
            <div class="rule-btn" @click="rulePop"></div>
        </div>


        <pai-button :disabled="signupBtnDisabled" btnTxt="" @click="goSignup"></pai-button>
        <div class="login-ask">
            <div class="ask-text" @click="login"></div>
        </div>   


        <popup v-model="ruleShow">
            <div class="rule-pop">
                <div class="rule-title-bar">
                    
                    <div class="rule-close" @click="closeRule"></div>
                </div>
                <div class="rule-content">
  
                            <p>
                                
                            </p>
                            
                       
                            <p>
                                <strong></strong>
                                <br>
                                
                            </p>
                            
                            <p>
                                
                            </p>
                        
                            <p>
                                
                            </p>
                </div> 
            </div>
        </popup>   
        </div>
      
    </div>
</template>
<script>
import Button from "@/components/button/button";
import Checkbox from "@/components/checkbox/checkbox";
import { signup,getSmCode } from "@/common/utils/api";
import { isMobile } from "@/common/utils/regex";
import { Toast } from "mand-mobile";
import Popup from "@/components/popup/popup";
export default {
  components: {
    [Button.name]: Button,
    [Checkbox.name]: Checkbox,
    [Popup.name]: Popup
  },
  data() {
    return {
      signupBtnDisabled: false,
      checkActive: true,
      user: {
        phone: "",
        password: "",
        smcode: ""
      },
      smcode:{
          mobile:'',
          type:'SIGNUP'
      },
      ruleShow: false,
      smcodebtnTxt: "",
      auth_time:60
    };
  },
  methods: {
    handleCheck(checkActive, event) {},
    login() {
      this.$router.push({ path: "/login" });
    },
    goSignup() {
      if (this.user.phone == "") {
        Toast.info("");
        return
      }else if(!isMobile(this.user.phone)){
        Toast.info("");
        return 
      } else if (this.user.password == "") {
        Toast.info("");
        return
      } else if (this.user.smcode == "") {
        Toast.info("");
        return
      } else {
        signup(this.user).then(res => {
          if(res.code==0){
              this.$router.push({path:'/'})
          }else{
              Toast.info(res.message)
          }
        });
      }
    },
    rulePop() {
      this.ruleShow = true;
    },
    closeRule() {
      this.ruleShow = false;
    },
    getCode() {
      if (this.user.phone == "") {
        Toast.info("");
        return
      } else if (this.user.password == "") {
        Toast.info("");
        return
      }
      if(this.auth_time<60) return 

    this.smcode.mobile=this.user.phone

      getSmCode(this.smcode).then(res => {
        if (res.code==0) {
          var auth_timetimer = setInterval(() => {
            this.auth_time--;
            this.smcodebtnTxt = this.auth_time + "";
            if (this.auth_time <= 0) {
              clearInterval(auth_timetimer);
              this.smcodebtnTxt = "";
            }
          }, 1000);
        } else {
          Toast.info(res.message);
        }
      });
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.signup-form
    position fixed
    left 0
    bottom 0
    right 0
    top 0
    z-index 3
    overflow scroll
    background #f0f0f0
    .fill
        height 0.7rem
    .header
        position fixed
        left 0
        top 0
        right 0
        bottom 0
        height 0.4rem
        padding 0.15rem 0
        background #fff
        color #333
        width 100%
        z-index 2
        box-shadow 0 0 5px #cdcdcd
        .title
            font-size 0.32rem
            text-align center
    .info-list
        display flex
        flex-direction row
        margin-top 0.15rem
        padding 0.25rem
        height 0.5rem
        background #fff
        border-radius 0.05rem
        &:first-child
            margin-top 0.45rem
    ._icon-username, ._icon-password
        width 0.5rem
        height 0.5rem
        line-height 0.5rem
        font-size 0.4rem
        color #9b9b9b
.input-item
    flex 1
    display flex
.input-item
    input
        border 0 none
        height 100%
        appearance none
        background-color transparent
        outline 0 none
        padding-left 0.1rem
        font-size 0.3rem
        flex 1
        color #333, ::input-placeholder
            color #9b9b9b
.rule, .login-ask
    margin-top 0.2rem
    margin-bottom 0.35rem
    display flex
    .rule-btn, .ask-text
        touch-action none
        color #666666
        font-size 0.24rem
        flex 1
        height 20px
        line-height 20px
        text-align left
        padding-left 10px
    .ask-text
        text-align center !important

.icon-sifa
    display block
    width 1.19rem
    height 1.19rem
    margin 0.9rem auto 0
.smcode-btn
    height 0.5rem
    line-height 0.5rem
    width 2.5rem
    font-size 0.3rem
    color #40b573
    text-align right
.rule-pop
    min-width 300px
    background #fff
    border-radius 5px
    .rule-title-bar
        box-shadow 0 0 10px rgba(0, 0, 0, 0.15)
        font-size 0.3rem
        padding 0.1rem 0
        height 0.5rem
        line-height 0.5rem
        text-align center
        position relative
    .rule-content
        padding 0.2rem 0.1rem
        overflow auto
    p
        text-indent 2em
        margin 0 0 10px
        font-size 0.2rem
        line-height 1.25
    .rule-close
        width 0.5rem
        height 0.5rem
        position absolute
        right 0.1rem
        top 0.1rem
        font-size 0.5rem
        cursor hand
</style>




